import { useEffect, useState } from 'react';
import { UserBankApi } from 'utils/Api';
import { Table } from 'antd';
const { Column } = Table;

const Bank = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    UserBankApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          id: item._id.slice(0, 7),
        }))
      );
    });
  }, []);

  return (
    <>
      <Table dataSource={tableData} rowKey="_id">
        <Column title="序号" dataIndex="id" />
        <Column title="姓名" render={(_,record) => record.user.nick} />
        <Column title="银行名称" render={(_,record) => record.bank.value} />
        <Column title="银行帐号"  render={(_,record) => record.bankAccount}/>
      </Table>
    </>
  );
};

export default Bank;
